.fade-in {
	animation: fadeIn 1s 1;
	-webkit-animation: fadeIn 1s 1;
}

.fade-in-m {
	animation: fadeIn 3s 1;
	-webkit-animation: fadeIn 3s 1;
}

.fade-in-l {
	animation: fadeIn 5s 1;
	-webkit-animation: fadeIn 5s 1;
}

@keyframes fadeIn {
	0% {
		opacity: 0%;
		-webkit-filter: blur(5px);
		        filter: blur(5px);
	}
	100% {
		opacity: 100%;
		-webkit-filter: blur(0px);
		        filter: blur(0px);
	}
}

@-webkit-keyframes fadeIn {
	0% {
		opacity: 0%;
		-webkit-filter: blur(5px);
		        filter: blur(5px);
	}
	100% {
		opacity: 100%;
		-webkit-filter: blur(0px);
		        filter: blur(0px);
	}
}

.fly-in-right {
	animation-name: flyInRight, fadeIn;
	animation-iteration-count: 1, 1;
	animation-duration: 1s, 1s;
	-webkit-animation-name: flyInRight, fadeIn;
	-webkit-animation-iteration-count: 1, 1;
	-webkit-animation-duration: 1s, 1s;
}

@keyframes flyInRight {
	0% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}

@-webkit-keyframes flyInRight {
	0% {
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}

.fly-in-left {
	animation-name: flyInLeft, fadeIn;
	animation-iteration-count: 1, 1;
	animation-duration: 1s, 1s;
	-webkit-animation-name: flyInLeft, fadeIn;
	-webkit-animation-iteration-count: 1, 1;
	-webkit-animation-duration: 1s, 1s;
}

@keyframes flyInLeft {
	0% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}

@-webkit-keyframes flyInLeft {
	0% {
		-webkit-transform: translateX(-100%);
		        transform: translateX(-100%);
	}
	100% {
		-webkit-transform: translateX(0%);
		        transform: translateX(0%);
	}
}

.margin-slide-down {
	animation-name: marginSlideDown, fadeIn;
	animation-iteration-count: 1, 1;
