#Blog::before {
	content: "";
	position: fixed;
	z-index: -1;
	background-image: url("/base_back.jpeg");
	background-repeat: no-repeat;
	background-size: 100% 100%;
	background-position: center;
}

#Blog {
	z-index: 0;
}

#Blog .smallImg {
	display: block;
	max-height: 300px;
}

#Blog .largeImg {
	width: 100%;
}
